import { fade } from './../animation';
import { Model, billing } from './../model';
import { FeederService } from './../services/feeder.service';
import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { roomModel } from '../model';

@Component({
  selector: 'app-room-details',
  templateUrl: './room-details.component.html',
  styleUrls: ['./room-details.component.css'], 
  animations:[fade]
})

export class RoomDetailsComponent implements OnInit {
  state: boolean=false;
  saveBill: { 'date': Date; 'meterReading': number; 'roomId': any; };
  toggle(){
    if(this.state){
      this.state=!this.state;
      document.getElementById('generateBlock').style.display='none';
    }
    else{
      this.state=!this.state;
      document.getElementById('generateBlock').style.display='block';
    }
  }

  private routerSub: Subscription;
  room:Observable<roomModel>;
  member:Observable<Model>;
  roomID:string;
  memberID:string;  
  bills: Observable<billing>;
  billDetails:{};

  meterValue: number;
  dues: number=0;  
  preReading: number=0;
  date:Date;
  memberName: string;
  roomNo: string;
  contactNo: any;
  rent: number=0;
  bill:number;

  constructor(private route: ActivatedRoute, private feeder: FeederService ) { }
 
  ngOnInit() {
    this.routerSub=this.route.params.subscribe(
      params=>{
        this.memberID=params['roomID'];        
        this.roomID=params['memberID'];
      }
    );
    
    this.bills=this.feeder.getBills(this.roomID);
    //console.log(this.bills);
    this.room= this.feeder.getRoomInfo(this.roomID);
    this.member= this.feeder.getPost(this.memberID);
    this.getValue();
  }

  generateBill(){
    this.bill=this.rent+this.dues+(this.meterValue-this.preReading)*8;
  }

  getValue(){
    this.bills.subscribe(response => {
        this.preReading= response[0].data.meterReading;
    });
    
    this.room.subscribe(response => {
      this.rent=response.rent;
      this.roomNo=response.roomNo;
    });

    this.member.subscribe(response => {
      this.memberName=response.name;
      this.contactNo=response.contactNo;
    });
  }

  postBill(roomID){
    this.billDetails={'roomId':roomID, 'Name':this.memberName, 'Rent':this.rent, 'date':this.date, 'meterReading':this.meterValue, 'previousReading': this.preReading, 'dues': this.dues, 'totalBill': this.bill};
    //console.log(this.billDetails);
    this.feeder.postBill(this.billDetails);
  }


}
 