import { Model } from './../model';
import { FeederService } from './../services/feeder.service';
import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-newroom',
  templateUrl: './newroom.component.html',
  styleUrls: ['./newroom.component.css']
})
export class NewroomComponent implements OnInit {
  rent:number;
  roomNo: number;
  memberID: string;
  memberName: string;
  members: any;
  roomData:{};

  constructor(private feeder: FeederService) { }

  ngOnInit() {
    this.members=this.feeder.getMembers();
  }

  addRoom(){
    this.roomData={'memberId':this.memberID, 'rent':this.rent, 'rentedBy':this.memberName, 'roomNo': this.roomNo};
    this.feeder.addRoom(this.roomData);
  }

  getMemberDetails(memberId, name){
    this.memberID=memberId;
    this.memberName=name;
  }
}
