
import { Injectable } from '@angular/core';
import {AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { Model, billing } from '../model';
import { map } from 'rxjs/operators'; 
import { roomModel } from '../model';


@Injectable({
  providedIn: 'root'
})

export class FeederService {

  membersDoc: AngularFirestoreCollection<Model>;
  members: any;
  
  postDoc: AngularFirestoreDocument<Model>;
  post: Observable<Model>;

  roomsDoc: AngularFirestoreCollection<roomModel>;
  rooms: any;

  roomDoc: AngularFirestoreDocument<roomModel>;
  room: Observable<roomModel>;

  billsDocs:AngularFirestoreCollection<billing>;
  bills:any;

  memberName:string;
  contactNo:string;
  permanentAddress:string;
  data={}
  
  constructor(private afs: AngularFirestore) { }

  ngOnInit() {
  }

  getMembers(){
    this.membersDoc=this.afs.collection('Members', ref=>ref.orderBy('name','asc'));
    this.members=this.membersDoc.snapshotChanges().pipe(map(actions=>{
      return actions.map(a=>{
        const data=a.payload.doc.data() as Model;
        const id =a.payload.doc.id;
        return {id, data};
      });
    })
    );
    return this.members;
  }

  getRooms(){
    this.roomsDoc=this.afs.collection('Rooms');
    this.rooms=this.roomsDoc.snapshotChanges().pipe(map(actions=>{
      return actions.map(a=>{
        const data=a.payload.doc.data() as roomModel;
        const id =a.payload.doc.id;
        return {id, data};
      });
    })
    );
    return this.rooms;
  }

  addRoom(data:{}){
    this.afs.collection('Rooms').add(data);
  }

  addMember(data){
    this.afs.collection('Members').add(data);
  }

  getPost(postID:string){    
    //console.log('post id:',postID);
    this.postDoc=this.afs.doc('Members/'+postID);
    this.post=this.postDoc.valueChanges();
    return this.post;
  }

  getRoomInfo(roomID:string){
    this.roomDoc=this.afs.doc('Rooms/'+roomID);
    this.room=this.roomDoc.valueChanges();
    //console.log('room id:', roomID);
    return this.room;
  }

  getBills(roomID){    
    this.billsDocs=this.afs.collection('Bills', ref=>ref.where('roomId', '==', roomID).orderBy('date', 'desc'));
    this.bills=this.billsDocs.snapshotChanges().pipe(map(actions=>{
      return actions.map(a=>{
        const data=a.payload.doc.data() as billing;
        const id =a.payload.doc.id;
        return {id, data};
      });
    })
    );
    return this.bills;
  }

  updateRentedBy(roomID, memberID, memberName){
    this.afs.collection('Rooms').doc(roomID).update({'memberId': memberID, 'rentedBy': memberName});
  }

  deletePost(postId){
    this.afs.doc('Members/'+postId).delete();
  }

  postBill(details:{}){
    this.afs.collection('Bills').add(details);
  }


  updateRent(roomId:string, roomRent: number){
    this.afs.collection('Rooms').doc(roomId).update({'rent':roomRent});
  }

}
