import { RoomsComponent } from './rooms/rooms.component';
import { AddMemberComponent } from './add-member/add-member.component';
import { MembersComponent } from './members/members.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DetailsComponent } from './details/details.component';
import { RoomDetailsComponent } from './room-details/room-details.component';
import { NewroomComponent } from './newroom/newroom.component';
import { UpdatesComponent } from './updates/updates.component';

const routes: Routes = [
  {path: '', redirectTo: '/rooms', pathMatch: 'full'},
  {path: 'rooms' , component: RoomsComponent },
  {path: 'members' , component: MembersComponent },
  {path: 'addMember', component: AddMemberComponent},  
  {path: 'updates/:roomId', component: UpdatesComponent},  
  {path: 'newRoom', component:NewroomComponent},
  {path: 'details/:PostId', component:DetailsComponent},
  {path: 'room-details/:memberID/:roomID', component:RoomDetailsComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
