import { trigger, transition, style, animate, state } from '@angular/animations';

export let fade = trigger('fade', [
    transition(':enter', [
      style({opacity: 0}),
      animate(800)
    ])
  ])

export let slide = trigger('fade', [
    transition(':enter', [
      style({opacity: 0, height: 10}),
      animate(800)
    ])
  ])