import { fade } from './../animation';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FeederService } from './../services/feeder.service';
import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'app-add-member',
  templateUrl: './add-member.component.html',
  styleUrls: ['./add-member.component.css'],
  animations: [fade]
})
export class AddMemberComponent implements OnInit {

  memberName:string;
  contactNo:string;
  permanentAddress:string;
  data={}

  constructor(private feeder: FeederService) { }

  ngOnInit() {
  }
  
  addMember(memberName, contactNo, permanentAddress){
    this.data={'name':this.memberName, 'contactNo':this.contactNo, 'permanentAddress': this.permanentAddress};
    this.feeder.addMember(this.data);
  }

}
